import { DATE_FORMAT_STRING, ORIGIN } from "@/config";
import { emitter } from "@/event-bus";
import { addDays } from "date-fns";
import { format } from "date-fns/esm";
import { computed, inject, reactive, ref } from "vue";
import { useRouter } from "vue-router";
import { useStore } from "vuex";
import useVuelidate from "@vuelidate/core";
import { required } from "@vuelidate/validators";
import { buildPdfUrl } from "@/service/utils";

const column = {
    employeeId: null,
    day1Stage: null,
    day2Stage: null,
    day3Stage: null,
    day4Stage: null,
    day5Stage: null,
    day6Stage: null,
    day7Stage: null,
    day8Stage: null,
    comment: null
};

export function useFertilizationInit() {
    const store = useStore();
    const router = useRouter();

    const showGrid = ref(false);
    const problem = ref(false);
    const isBornCycle = computed(() => store.getters["artCycle/isBornCycle"]);

    const embryoDetailDialog = ref(false);

    const selectedEmbryo = reactive({
        day: null,
        date: null,
        fertId: null,
        fertLineId: null,
        occId: null
    });

    const setSelectedEmbryo = (fertDay, fertLine) => {
        selectedEmbryo.day = fertDay.day;
        selectedEmbryo.date = fertDay.date;
        selectedEmbryo.fertId = fertLine.fertId;
        selectedEmbryo.fertLineId = fertLine.lineId;
        selectedEmbryo.occId = fertLine[`day${fertDay.day}Id`]
            ? fertLine[`day${fertDay.day}Id`]
            : 0;
    };

    const embryoDetail = reactive({
        fertLineId: null,
        dayNo: null,
        oocId: 0,
        cultureDay: selectedEmbryo.date,
        embryoAge: null,
        stage: null,
        grade: null,
        score: null,
        time: null,
        oocGroup: null,
        clinician: null,
        embryologist: null,
        fate: null,
        straw: null,
        frozenDate: null,
        thawingDate: null,

        uniformCells: null,
        cellsMultinucleation: null,
        fragmentation: null,
        cellsCompaction: null,
        asphericalForm: null,
        vacuolisation: null,
        granularCytoplasm: null,
        abnormalZona: false,
        fragmentedPolarBody: false,
        fragmentsInPus: false,

        oocDiameter: null,
        thicknessZpN: null,
        thicknessZpS: null,
        thicknessZpE: null,
        thicknessZpW: null,

        incubator: null,
        description: null,
        newImage: null,
        images: []
    });

    const getEmbryoDetail = () => {
        let occId = selectedEmbryo.occId;

        let lineIndex = store.state.fertilization.fertilizationLines.findIndex(
            (item) => item.lineId == selectedEmbryo.fertLineId
        );

        let { defaultClinician, defaultEmbryologist } = {
            ...store.state.fertilization.fertilizationLines[lineIndex]
        };

        if (occId == 0) {
            embryoDetail.dayNo = null;
            embryoDetail.oocId = null;
            embryoDetail.cultureDay = null;
            embryoDetail.embryoAge = null;
            embryoDetail.stage = null;
            embryoDetail.grade = null;
            embryoDetail.score = null;
            embryoDetail.time = null;
            embryoDetail.oocGroup = null;
            embryoDetail.clinician = defaultClinician;
            embryoDetail.embryologist = defaultEmbryologist;
            embryoDetail.uniformCells = null;
            embryoDetail.cellsMultinucleation = null;
            embryoDetail.fragmentation = null;
            embryoDetail.cellsCompaction = null;
            embryoDetail.asphericalForm = null;
            embryoDetail.vacuolisation = null;
            embryoDetail.granularCytoplasm = null;
            embryoDetail.oocDiameter = null;
            embryoDetail.thicknessZpN = null;
            embryoDetail.thicknessZpS = null;
            embryoDetail.thicknessZpE = null;
            embryoDetail.thicknessZpW = null;
            embryoDetail.incubator = null;
            embryoDetail.description = null;
            embryoDetail.newImage = null;
            embryoDetail.images = [];
            embryoDetail.abnormalZona = false;
            embryoDetail.fragmentedPolarBody = false;
            embryoDetail.fragmentsInPus = false;
            return;
        }

        store
            .dispatch("fertilization/getFertlizationEmbryoDetail", occId)
            .then((data) => {
                embryoDetail.dayNo = data.dayNo;
                embryoDetail.oocId = data.oocId;
                embryoDetail.cultureDay = data.cultureDay;
                embryoDetail.embryoAge = data.embryoAge;
                embryoDetail.stage = data.stage;
                embryoDetail.grade = data.grade;
                embryoDetail.score = data.score;
                embryoDetail.time = data.time;
                embryoDetail.oocGroup = data.oocGroup;
                embryoDetail.clinician = data.clinician
                    ? data.clinician
                    : defaultClinician;
                embryoDetail.embryologist = data.embryologist
                    ? data.embryologist
                    : defaultEmbryologist;
                embryoDetail.uniformCells = data.uniformCells;
                embryoDetail.cellsMultinucleation = data.cellsMultinucleation;
                embryoDetail.fragmentation = data.fragmentation;
                embryoDetail.cellsCompaction = data.cellsCompaction;
                embryoDetail.asphericalForm = data.asphericalForm;
                embryoDetail.vacuolisation = data.vacuolisation;
                embryoDetail.granularCytoplasm = data.granularCytoplasm;
                embryoDetail.oocDiameter = data.oocDiameter;
                embryoDetail.thicknessZpN = data.thicknessZpN;
                embryoDetail.thicknessZpS = data.thicknessZpS;
                embryoDetail.thicknessZpE = data.thicknessZpE;
                embryoDetail.thicknessZpW = data.thicknessZpW;
                embryoDetail.incubator = data.incubator;
                embryoDetail.description = data.description;
                embryoDetail.newImage = null;
                embryoDetail.images = data.images;
                embryoDetail.abnormalZona = data.abnormalZona;
                embryoDetail.fragmentedPolarBody = data.fragmentedPolarBody;
                embryoDetail.fragmentsInPus = data.fragmentsInPus;
            })
            .finally(() => {});
    };

    const onCellClick = (fertDay, fertLine) => {
        setSelectedEmbryo(fertDay, fertLine);
    };

    const openEmbryoDetailDialog = (fertDay, fertLine) => {
        setSelectedEmbryo(fertDay, fertLine);
        embryoDetailDialog.value = true;
    };
    const closeEmbryoDetailDialog = () => (embryoDetailDialog.value = false);

    const getFertilization = () => {
        if (store.state.artCycle.selectedCycle == null) {
            problem.value = true;
            return;
        }
        emitter.emit("app:show-page-loading", true);

        store
            .dispatch(
                "fertilization/getFertilization",
                store.state.artCycle.selectedCycle.cycleId
            )
            .then(() => {
                showGrid.value = true;
            })
            .catch(() => {
                router.push("/fertilization/create");
                showGrid.value = false;
            })
            .finally(() => {
                emitter.emit("app:show-page-loading", false);
            });
    };

    return {
        store,
        isBornCycle,
        problem,
        showGrid,
        embryoDetailDialog,
        openEmbryoDetailDialog,
        closeEmbryoDetailDialog,
        getFertilization,
        selectedEmbryo,
        setSelectedEmbryo,
        embryoDetail,
        getEmbryoDetail,
        onCellClick
    };
}

export function useFertilizationGridInit() {
    const store = useStore();
    const fertId = ref();

    const gridDesc = ref(false);
    const sortToggle = () => (gridDesc.value = !gridDesc.value);
    const fertilizationGrid = computed(() => {
        if (gridDesc.value == true) {
            return store.state.fertilization.fertilizationLines.sort(
                (a, b) => b.noEmbryo - a.noEmbryo
            );
        } else {
            return store.state.fertilization.fertilizationLines.sort(
                (a, b) => a.noEmbryo - b.noEmbryo
            );
        }
    });
    const createFertilizationLineDialog = ref(false);

    const fertilizationGridDays = reactive([
        { day: 0, date: null, editable: false },
        { day: 1, date: null, editable: false },
        { day: 2, date: null, editable: false },
        { day: 3, date: null, editable: false },
        { day: 4, date: null, editable: false },
        { day: 5, date: null, editable: false },
        { day: 6, date: null, editable: false },
        { day: 7, date: null, editable: false }
    ]);

    const selectedEmbryo = inject("selectedEmbryo");
    const setSelectedEmbryo = inject("setSelectedEmbryo");

    const moveGridCell = (direction) => {
        const days = [...fertilizationGridDays];
        const lineIdx = fertilizationGrid.value.findIndex(
            (item) => item.lineId == selectedEmbryo.fertLineId
        );
        let line = fertilizationGrid.value[lineIdx];

        const dayIdx = days.findIndex((item) => item.day == selectedEmbryo.day);
        let day = days[dayIdx];

        switch (direction) {
            case "up":
                line = fertilizationGrid.value[lineIdx - 1];
                break;
            case "down":
                line = fertilizationGrid.value[lineIdx + 1];
                break;
            case "left":
                if (dayIdx != 3) {
                    day = days[dayIdx - 1];
                } else {
                    day = days[dayIdx - 2];
                }
                break;
            case "right":
                if (dayIdx != 1) {
                    day = days[dayIdx + 1];
                } else {
                    day = days[dayIdx + 2];
                }
                break;
            default:
                console.log("error");
        }

        if (line != undefined && day != undefined) {
            setSelectedEmbryo(day, line);
        } else {
            console.log(line, day);
        }
    };

    const fillDaysRow = (val) => {
        fertilizationGridDays[0].date = val.fertDate0;
        fertilizationGridDays[1].date = val.fertDate1;
        fertilizationGridDays[2].date = val.fertDate2;
        fertilizationGridDays[3].date = val.fertDate3;
        fertilizationGridDays[4].date = val.fertDate4;
        fertilizationGridDays[5].date = val.fertDate5;
        fertilizationGridDays[6].date = val.fertDate6;
        fertilizationGridDays[7].date = val.fertDate7;
        getFertilizationLines(val.fertId);
        fertId.value = val.fertId;
    };

    const embryoDetailDialog = inject("embryoDetailDialog");

    const openEmbryoDetailDialog = inject("openEmbryoDetailDialog");
    const closeEmbryoDetailDialog = inject("closeEmbryoDetailDialog");

    const ferilizationLine = reactive({
        lineId: 0,
        fate: null,
        strawId: null,
        frozenDate: null,
        thawingDate: null,
        comment: null,
        noEmbryo: null,
        cycleType: null
    });

    const createFetilizationRow = () => {
        ferilizationLine.lineId = 0;
        ferilizationLine.fate = null;
        ferilizationLine.strawId = null;
        ferilizationLine.frozenDate = null;
        ferilizationLine.thawingDate = null;
        ferilizationLine.comment = null;
        ferilizationLine.noEmbryo =
            store.state.fertilization.fertilizationLines.length + 1;
        ferilizationLine.cycleType = null;

        createFertilizationLineDialog.value = true;
    };

    const editFetilizationRow = (line) => {
        ferilizationLine.lineId = line.lineId;
        ferilizationLine.fate = line.fate;
        ferilizationLine.strawId = line.strawId;
        ferilizationLine.frozenDate = line.frozenDate
            ? new Date(line.frozenDate)
            : null;
        ferilizationLine.thawingDate = line.thawDate
            ? new Date(line.thawDate)
            : null;
        ferilizationLine.comment = line.comment;
        ferilizationLine.noEmbryo = line.noEmbryo;
        ferilizationLine.cycleType = line.cycleType;

        createFertilizationLineDialog.value = true;
    };

    const getFertilizationLines = (fertId) => {
        store
            .dispatch("fertilization/getFertilizationLines", fertId)
            .then(() => {})
            .catch(() => {})
            .finally(() => {});
    };

    const showFateInGrid = (line, col) => {
        if (col.day != 1) {
            return line[`day${col.day + 1}Stage`] == null ? line.fate : "";
        } else {
            return line[`day${col.day + 2}Stage`] == null ? line.fate : "";
        }
    };

    const getFertilization = inject("getFertilization");
    const updateFertilization = (item) => {
        const payload = { ...store.state.fertilization.fertilization };
        payload[`fertDate${item.day}`] = format(item.date, DATE_FORMAT_STRING);
        store
            .dispatch("fertilization/updateFertlization", payload)
            .then(() => {
                return getFertilization();
            })
            .then(() => {
                item.editable = false;
            });
    };

    const onGridCellClick = inject("onCellClick");

    const openPdfReport = () => {
        const params = {
            cycleId: store.state.artCycle.selectedCycle.cycleId,
            ecId: fertId.value,
            pDate: store.state.artCycle.selectedCycle.cycleDate
        };
        let url = buildPdfUrl("Fertilization_Report", params);
        window.open(url);
    };

    return {
        store,
        fertilizationGrid,
        gridDesc,
        sortToggle,
        createFetilizationRow,
        createFertilizationLineDialog,
        editFetilizationRow,
        ferilizationLine,
        fertilizationGridDays,
        getFertilizationLines,
        moveGridCell,
        embryoDetailDialog,
        openEmbryoDetailDialog,
        closeEmbryoDetailDialog,
        fillDaysRow,
        showFateInGrid,
        updateFertilization,
        onGridCellClick,
        selectedEmbryo,
        openPdfReport
    };
}

export function useEmbryoDetailDialogInit() {
    const store = useStore();
    const embryoDetailDialog = inject("embryoDetailDialog");
    const closeEmbryoDetailDialog = inject("closeEmbryoDetailDialog");

    const selectedEmbryo = inject("selectedEmbryo");

    const embryoDetail = inject("embryoDetail");

    const loading = ref(false);

    const saveEmbryoDetail = () => {
        const formData = {
            ...embryoDetail,
            fertLineId: selectedEmbryo.fertLineId,
            dayNo: selectedEmbryo.day,
            cultureDay: selectedEmbryo.date,
            oocId: selectedEmbryo.occId,
            embryoAge: selectedEmbryo.day
        };
        loading.value = true;
        store
            .dispatch("fertilization/saveFertlizationEmbryoDetail", formData)
            .then((data) => {
                embryoDetail.newImage = null;
                embryoDetail.occId = data.oocId;
                selectedEmbryo.occId = data.oocId;
                embryoDetail.images.push(data.image);
            })
            .catch((err) => {
                console.log(err);
            })
            .finally(() => {
                loading.value = false;
            });
    };

    const getEmbryoDetail = inject("getEmbryoDetail");

    return {
        embryoDetailDialog,
        closeEmbryoDetailDialog,
        embryoDetail,
        saveEmbryoDetail,
        selectedEmbryo,
        getEmbryoDetail,
        loading
    };
}

export function useTreeViewInit() {
    const store = useStore();
    const embryoDetail = inject("embryoDetail");
    const treeViewData = reactive([
        {
            key: "stage",
            data: {
                name: "Stage",
                value: computed(() => embryoDetail.stage)
            }
        },
        {
            key: "embryoCellD",
            data: {
                name: "Embryo Cell [d]",
                value: computed(() => {
                    return embryoDetail.oocId != 0 ? embryoDetail.oocId : "";
                })
            }
        },
        {
            key: "score",
            data: {
                name: "Score",
                value: computed(() => embryoDetail.score)
            }
        },
        {
            key: "scoreTime",
            data: {
                name: "Score Time",
                value: computed(() => embryoDetail.time)
            }
        },
        {
            key: "occGroup",
            data: {
                name: "Occ. Group",
                value: computed(() => embryoDetail.oocGroup)
            }
        },
        {
            key: "physician",
            data: {
                name: "Physician",
                value: computed(() => {
                    let index = store.state.provider.providers.findIndex(
                        (item) => item.id == embryoDetail.clinician
                    );

                    if (index >= 0) {
                        return store.state.provider.providers[index].name;
                    } else {
                        ("");
                    }
                })
            }
        },
        {
            key: "embryologist",
            data: {
                name: "Embryologist",
                value: computed(() => {
                    let index = store.state.provider.providers.findIndex(
                        (item) => item.id == embryoDetail.embryologist
                    );

                    if (index >= 0) {
                        return store.state.provider.providers[index].name;
                    } else {
                        ("");
                    }
                })
            }
        },
        {
            key: "note",
            data: {
                name: "Note",
                value: null
            }
        },
        {
            key: "incubator",
            data: {
                name: "Incubator",
                value: null
            }
        },
        {
            key: "qMData",
            data: {
                name: "QM Data",
                value: null
            }
        }
    ]);

    const image = computed(() => {
        if (embryoDetail.images.length == 0) {
            return null;
        } else {
            let img = embryoDetail.images[embryoDetail.images.length - 1];
            return `${ORIGIN}/${img}`;
        }
    });

    const openImage = () => {
        window.open(
            image.value,
            "DescriptiveWindowName",
            "left=100,top=100,width=1200,height=800"
        );
    };

    return { treeViewData, image, openImage };
}

export function useCraeteFertilizationFromInit() {
    const store = useStore();
    const router = useRouter();

    const selectedCycle = computed(() => store.state.artCycle.selectedCycle);

    const form = reactive({
        fertDate0: null,
        fertTime0: null,
        fertInit0: null,

        fertDate1: null,
        fertTime1: null,
        fertInit1: null,

        fertDate2: null,
        fertTime2: null,
        fertInit2: null,

        fertDate3: null,
        fertTime3: null,
        fertInit3: null,

        fertDate5: null,
        fertTime5: null,
        fertInit5: null,

        fertDate6: null,
        fertTime6: null,
        fertInit6: null,

        fertDate7: null,
        fertTime7: null,
        fertInit7: null,

        comment: null,
        expComment: null
    });

    const rules = {
        fertDate0: { required },
        fertDate1: { required },
        fertDate2: { required },
        fertDate3: { required },
        fertDate4: { required },
        fertDate5: { required },
        fertDate6: { required },
        fertDate7: { required }
    };

    const v$ = useVuelidate(rules, form);
    const submitted = ref(false);

    const createFertilization = () => {
        submitted.value = true;
        if (v$.value.$invalid) {
            return;
        }

        let formData = {
            cycleId: selectedCycle.value.cycleId,
            patientId: selectedCycle.value.patientId,
            ...form,
            fertDate0: form.fertDate0
                ? format(form.fertDate0, DATE_FORMAT_STRING)
                : null,
            fertDate1: form.fertDate1
                ? format(form.fertDate1, DATE_FORMAT_STRING)
                : null,
            fertDate2: form.fertDate2
                ? format(form.fertDate2, DATE_FORMAT_STRING)
                : null,
            fertDate3: form.fertDate3
                ? format(form.fertDate3, DATE_FORMAT_STRING)
                : null,
            fertDate4: form.fertDate4
                ? format(form.fertDate4, DATE_FORMAT_STRING)
                : null,
            fertDate5: form.fertDate5
                ? format(form.fertDate5, DATE_FORMAT_STRING)
                : null,
            fertDate6: form.fertDate6
                ? format(form.fertDate6, DATE_FORMAT_STRING)
                : null,
            fertDate7: form.fertDate7
                ? format(form.fertDate7, DATE_FORMAT_STRING)
                : null
        };
        store
            .dispatch("fertilization/createFertlization", formData)
            .then(() => {
                router.push("/fertilization");
            })
            .catch(() => {})
            .finally(() => {});
    };

    const populateDates = (newValue) => {
        if (!form.fertDate1) {
            form.fertDate1 = addDays(form.fertDate0, 1);
        }
        if (!form.fertDate2) {
            form.fertDate2 = addDays(form.fertDate0, 2);
        }
        if (!form.fertDate3) {
            form.fertDate3 = addDays(form.fertDate0, 3);
        }
        if (!form.fertDate4) {
            form.fertDate4 = addDays(form.fertDate0, 4);
        }
        if (!form.fertDate5) {
            form.fertDate5 = addDays(form.fertDate0, 5);
        }
        if (!form.fertDate6) {
            form.fertDate6 = addDays(form.fertDate0, 6);
        }
        if (!form.fertDate7) {
            form.fertDate7 = addDays(form.fertDate0, 7);
        }
    };

    return { form, createFertilization, populateDates, submitted, v$ };
}

export function useFertilizationLineCreateDialog() {
    const store = useStore();

    const createFertilizationLineDialog = inject(
        "createFertilizationLineDialog"
    );

    const loading = ref(false);
    const form = inject("ferilizationLine");

    const fateOptions = reactive([
        "Frozen",
        "Discard",
        "Culture",
        "Donate",
        "Thawed",
        "Transfer",
        "Sent Out"
    ]);

    const cycleTypeOptions = computed(() =>
        store.state.artCycle.cycleTypeOptions.map((item) => item.value)
    );

    const save = () => {
        let fertilization = store.state.fertilization.fertilization;
        const formData = {
            lineId: form.lineId,
            fertilizationId: fertilization.fertId,
            cycleId: fertilization.cycleId,
            noEmbryo: form.noEmbryo,
            ...form,
            frozenDate: form.frozenDate
                ? format(form.frozenDate, DATE_FORMAT_STRING)
                : null,
            thawDate: form.thawingDate
                ? format(form.thawingDate, DATE_FORMAT_STRING)
                : null
        };
        loading.value = true;
        store
            .dispatch("fertilization/createFertilizationLine", formData)
            .then(() => {})
            .catch(() => {
                console.log("err");
            })
            .finally(() => {
                createFertilizationLineDialog.value = false;
                loading.value = false;
            });
    };

    return {
        form,
        fateOptions,
        save,
        loading,
        createFertilizationLineDialog,
        cycleTypeOptions
    };
}
